<template>
	<div class="card" v-if="this.$store.state.featureCollectionToEdit">
		<h2 v-if="!renaming">Editing {{ newName }}</h2>
        <h2 v-show="renaming">Editing <input v-model="newName" ref="in" class="renameInput"></h2>

		<div class="flex-container">
			<button class="btn" @click="changeName()" v-if="!renaming">Change Name</button>
            <button class="btn" @click="saveNewName()" v-else>Save name</button>
			<button class="btn" @click="removePlekollection()">
				Delete Plekollection
			</button>
		</div>
		<div
			class="list flex-container"
			v-for="(f, i) in this.$store.state.featureCollectionToEdit.features"
		>
			{{ f.properties.name }}
			<button @click="removeFeature(f)" class="btn3">-</button>
		</div>
		<button class="btn btn4" @click="save()">save</button>
	</div>
</template>

<script>
var api = require("@/utils/api");
export default {
	data() {
		return {
			newName: this.$store.state.selectedPlekollection.name,
			renaming: false,
		};
	},
	methods: {
		changeName() {
			this.renaming = true;
            this.$refs.in.focus();
		},
        saveNewName(){
            this.renaming=false;
            api.renamePlekollection(this.newName)
        },
		removeFeature(feature) {
			if (
				confirm(
					`Delete "${feature.properties.name}" from "${this.$store.state.featureCollectionToEdit.props.name}"?`
				)
			) {
				this.$store.state.featureCollectionToEdit.features =
					this.$store.state.featureCollectionToEdit.features.filter(
						(f) => {
							return f.properties.name != feature.properties.name;
						}
					);
				//this.$store.commit("reloadMap");
			} else {
				console.log("canceled");
			}
		},
		removePlekollection() {
			if (
				confirm(
					`Delete "${this.$store.state.featureCollectionToEdit.props.name}"?`
				)
			) {
				api.removePlekollection();
			}
		},
		save() {
			api.editPlekollectionFlow(() => {});
		},
	},
};
</script>

