<template>
	<div class="card">
		<div class="flex-container">
			<h2>My plekjes</h2>
			<SegmentButton
				:labels="['Untagged', 'All']"
				v-on:pick="setListType"
                :picked="mode"
			></SegmentButton>
		</div>
		<div v-if="no_untagged && mode == 'Untagged'">
            <p>No untagged plekjes</p>
        </div>
		<div class="list" v-for="(p, j) in plekjesToDisplay">
			<div @click="selectPlekje(p, j)">
				<div v-if="j == selected_j" class="card">
					<div class="flex-container">
						<h3>{{ p.name }}</h3>
						<span>
                            <button @click="edit()" class="btn">Edit</button>
							&nbsp;
							<button @click="map()" class="btn">Map</button>
						</span>
					</div>
					<div v-if="tagged_in.length > 0">
						<h4>Tagged in</h4>
						<div class="list" v-for="pc in tagged_in">
							<div @click="removeFrom(p, pc)" class="flex-container">
								{{ pc.name }}
							</div>
						</div>
					</div>
				</div>
				<div v-else>
					{{ p.name }}
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import SegmentButton from "./SegmentButton.vue";
var api = require("@/utils/api");
export default {
	components: {
		SegmentButton,
	},
	data() {
		return {
			selected_j: null,
			tagged_in: [],
			plekjesToDisplay: [],
			no_untagged: false,
			mode: "Untagged",
		};
	},
	methods: {
		selectPlekje(p, j) {
			if (this.selected_j != j) {
				this.tagging = false;
				this.tagged_in = [];
				this.selected_j = j;
				api.setSelectedPlekje(p._id, (pcs) => {
					this.tagged_in = pcs;
				});
			}
		},
		map() {
			api.addSinglePlekjeToMap();
		},
        edit(){
            this.$store.state.editingPlekje = true;
        },
		setListType(e,setup) {
			this.selected_j = null;
			if (e == "All") {
				this.mode = "All";
				this.plekjesToDisplay = this.$store.state.userData.my_plekjes;
			}
			if (e == "Untagged") {
				this.mode = "Untagged";
				// all plekjes that have no tags
				let ids = this.$store.state.userData.my_plekjes.map(
					(p) => p._id
				);
				api.getPlekjesWithoutTags(ids, (plekjes) => {
					this.plekjesToDisplay = plekjes;
					if (plekjes.length == 0) {
						this.no_untagged = true;
                        if(setup){
                            this.setListType("All");
                        }
					} else {
						this.no_untagged = false;
					}
				});
			}
		},
	},
	mounted() {
		this.setListType("Untagged",true);
	},
};
</script>

