<template>
	<div class="card">
		<h2 v-if="!renaming">Editing {{ newName }}</h2>
		<h2 v-show="renaming">Editing <input v-model="newName" ref="in" class="renameInput"></h2>
		<!-- <h2>Editing {{ $store.state.selectedPlekje.name }}</h2> -->
		<div class="flex-container">
			<button class="btn" @click="changeName()" v-if="!renaming">Change Name</button>
            <button class="btn" @click="saveNewName()" v-else>Save name</button>
			<button
				class="btn"
				@click="tag()"
				v-bind:class="{ btnActive: tagging }"
			>
				Tag
			</button>
			<button class="btn" @click="deletePlekje()">Delete Plekje</button>
		</div>
		<div v-if="tagged_in.length > 0">
			<h4>Tagged in</h4>
			<div class="list" v-for="pc in tagged_in">
				<div @click="removeFrom(pc)" class="flex-container">
					{{ pc.name }}
					<button class="btn3" v-if="tagging">-</button>
				</div>
			</div>
		</div>
		<div v-if="tagging && not_tagged_in.length > 0">
			<h4>Add '{{ $store.state.selectedPlekje.name }}' to</h4>
			<div class="list" v-for="(pc2, j) in not_tagged_in">
				<div @click="addTo(pc2)" class="flex-container">
					{{ pc2.name }}
					<button class="btn3 btnPlus" v-if="tagging">+</button>
				</div>
			</div>
		</div>
		<br />
		<button class="btn btn4" @click="save()">OK</button>
	</div>
</template>

<script>
var api = require("@/utils/api");
export default {
	data() {
		return {
			tagged_in: [],
			not_tagged_in: [],
			tagging: false,
            newName: this.$store.state.selectedPlekje.name,
			renaming: false,
		};
	},
	methods: {
		changeName() {
			this.renaming = true;
            this.$refs.in.focus();
		},
        saveNewName(){
            this.renaming=false;
            api.renamePlekje(this.newName)
        },
		deletePlekje() {
			if (confirm(`Delete "${this.$store.state.selectedPlekje.name}"?`)) {
				api.removePlekje();
			}
		},
		tag() {
			this.tagging = !this.tagging;
		},
		addTo(pc) {
			console.log(`adding to ${pc.name}`);
			this.not_tagged_in = this.not_tagged_in.filter(
				(x) => x._id != pc._id
			);
			this.tagged_in.push(pc);
			api.updateSelectedPlekje(pc, "add");
		},
		removeFrom(pc) {
			console.log(`removing from ${pc.name}`);
			this.tagged_in = this.tagged_in.filter((x) => x._id != pc._id);
			this.not_tagged_in.push(pc);
			api.updateSelectedPlekje(pc, "remove");
		},
		save() {
			this.$store.state.editingPlekje = false;
		},
	},
	mounted() {
		api.getPlekollections(this.$store.state.selectedPlekje.tags, (pcs) => {
			this.tagged_in = pcs;
			this.not_tagged_in = [];
			let pc_names = pcs.map((x) => x.name);
			this.$store.state.userData.my_plekollections.forEach((pc) => {
				if (!pc_names.includes(pc.name)) {
					this.not_tagged_in.push(pc);
				}
			});
		});
	},
};
</script>

