<template>
	<div class="wrap">
		<div v-for="(label, i) in labels">
            <input
					type="radio"
                    :value = "label"
					v-model="picked"
					@change="emitToParent"
                    :id="'rdb' + i" :checked="i == 0"
				/>
			<label class="btn" :for="'rdb' + i" v-bind:class="{ btnActive: picked==label}">
				{{ label }}
			</label>
            &nbsp;
		</div>
	</div>
</template>

<script>
export default {
	emits: ["pick"],
	props: {
		labels: Array,
        picked:String
	},
	data() {
		return {
		};
	},
	methods: {
		emitToParent(event) {
			this.$emit("pick", this.picked);
		},
	},
};
</script>

<style scoped>
.wrap {
	position: relative;
	z-index: 5;
	display: flex;
}

input {
	display: none;
}
</style>