<template>
	<div id="cont">
		<div class="card" style="text-align: center" @click="goAbout()">
			<h1>Plekjes</h1>
			<p>{{version}} pre-alpha</p>
		</div>
		<div v-if="about">
			<About></About>
		</div>
		<div v-else-if="this.$store.state.userIsAuthenticated">
			<UserData v-if="this.$store.state.userEmail"></UserData>
			<div v-else>
				<p>Loading profile...</p>
			</div>
		</div>

		<div v-else>
			<button class="btn" @click="this.$store.dispatch('auth0Login')">
				Log in
			</button>
		</div>
	</div>
</template>

<script>
import { version } from "../../package.json";
import UserData from "../components/UserData.vue";
import About from "../components/About.vue";

export default {
	components: {
		UserData,
		About,
	},
	data() {
		return {
			about: false,
            version: version
		};
	},
	methods: {
		goAbout() {
			this.about = !this.about;
			// console.log(this.about);
		},
	},
	beforeCreate() {
		if (!this.$store.state.userEmail) {
			this.$store.dispatch("auth0getUserEmail");
		}
	},
};
</script>

<style>
#cont {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 15%;
	/* border: 1px solid black; */
}

.flex-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
</style>